import { XMarkIcon } from '@heroicons/react/24/outline'
import cx from 'classnames'

export type Message = {
  id: string
  type: 'success' | 'warning' | 'error'
  title: string
  content?: string | React.ReactNode
  className?: string
}

type Props = Message & {
  onClose?: () => void
}

const CheckCircleIcon = () => (
  <svg className="absolute top-[18px] left-6 w-6 text-success" fill="currentColor" viewBox="0 0 20 20">
    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
  </svg>
)

const ErrorCircleIcon = () => (
  <svg className="absolute top-[18px] left-6 w-6 text-danger" fill="currentColor" viewBox="0 0 20 20">
    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
  </svg>
)

const WarningCircleIcon = () => (
  <svg className="absolute top-[18px] left-6 w-6 text-warning" fill="currentColor" viewBox="0 0 20 20">
    <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
  </svg>
)

const Message: React.FC<Props> = ({
  type,
  title,
  content,
  onClose,
  className,
}) => {
  const renderIcon = () => {
    switch (type) {
      case 'success':
        return <CheckCircleIcon />
      case 'warning':
        return <WarningCircleIcon />
      case 'error':
      default:
        return <ErrorCircleIcon />
    }
  }

  return (
    <div
      className={cx(
        'relative pl-[60px] pr-[90px] py-[22px] w-full bg-background rounded-xl border border-light-4 shadow-[0_8px_16px_rgba(0,0,0,0.08)]',
        content ? 'sm:w-[600px]' : 'sm:w-[434px]',
        className
      )}
    >
      {renderIcon()}
      <div>
        <h4 className="font-semibold leading-[100%]">{title}</h4>
        {content && (
          <div className="mt-3 text-light-dark text-sm leading-[150%]">
            {content}
          </div>
        )}
      </div>
      {onClose && (
        <div
          className="absolute top-2.5 right-6 flex items-center justify-center w-10 h-10 bg-transparent rounded-full cursor-pointer transition-colors hover:bg-light-4-tint"
          onClick={onClose}
        >
          <XMarkIcon className="w-8 stroke-1" />
        </div>
      )}
    </div>
  )
}

export default Message
